<!-- 报表-采购统计 -->
<template>
	<view> <cu-custom :bgColor="NavBarColor" :isBack="true" backRouterName="index">
			<block slot="backText">返回</block>
			<block slot="content">采购统计列表</block>
		</cu-custom>
		<scroll-view>
			<div>
				<hr>
				<view class="stock-list" :style="[{animation: 'show 0.5s 1'}]">
					<view class="stock-item" v-for="item in buyIn" :key="item.materialName">
						<view class="item-row">
							<text class="label">名称：</text>
							<text class="value name">{{ item.materialName || '-' }}</text>

						</view>
						<view class="item-row">							
						<text class="label">条码：</text>
							<text class="value">{{ item.barCode || '-' }}</text>
<!-- 							<text class="label">采购价：</text>
							<text class="value">{{ item.purchaseDecimal.toFixed(2) || '-' }}</text> -->
							<text class="label">单位：</text>
							<text class="value">{{ item.materialUnit || '-' }}</text>
						</view>
						<view class="item-row">
							<text class="label">采购数量：</text>
							<text class="value">{{item.inSum || '-' }}</text>
							<text class="label">采购金额：</text>
							<text class="value">¥{{item.inSumPrice.toFixed(2) || '-' }}</text>
						</view>
					</view>
				</view>
			</div>
		</scroll-view>
		<hr>
			<view class="sum_stock-item" :style="[{animation: 'show 0.5s 1'}]">
			<text class="label">			总金额：</text>
			<text class="sumvalue">¥{{realityPriceTotal||'0.00'}}</text>
		</view>
	</view>

</template>

<script>
	export default {
		data() {
			return {
				buyIn: [],
				realityPriceTotal: '',
				buyInUrl: '/depotItem/buyIn'
			}
		},
		onLoad() {
			this.loadinfo()
		},
		methods: {

			loadinfo() {
				this.$http.get(this.buyInUrl, {
					params: {
						currentPage: 1,
						pageSize: 99,
						materialParam: '',
						beginTime: '2025-01-14',
						endTime: '2025-04-14',
						mpList: ''
					}
				}).then(res => {
					if (res.data.code == "200") {
						let result = res.data.data;
						this.realityPriceTotal = result.realityPriceTotal;
						this.buyIn = result.rows;
					}
				}).catch(e => {
					console.log("请求错误", e)
				})
			}
		}
	}
</script>

<style scoped>
	.stock-list {
		border-radius: 5px;
		overflow: hidden;
	}

	.stock-item {
		background: #fff;
		padding: 3px;
		margin-bottom: 5px;
		border-radius: 5px;
		box-shadow: 0 2px 6px rgba(0, 0, 0, 0.05);
	}

	.item-row {
		display: flex;
		margin-bottom: 6px;
		font-size: 14px;
	}

	.label {
		color: #000000;
		width: 80px;
		font-family: 'HarmonyOS Sans Black';
		text-align: right;
	}

	.value {
		color: #ff0000;
		flex: 1;
		font-size: 10px;
		margin-top: 2px;
		text-align: left;
	}
	.name {
		font-weight: bold;
		color: #0000ff;
	}
	.sumvalue {
		color: #ff0000;
		flex: 1;
		font-size: 18px;
		margin-top: 2px;
		padding-left: 150px;
		text-align: right;
	}

	.sum_stock-item {
		background: #06f0be;
		padding: 3px;
		margin-bottom: 5px;
		border-radius: 5px;
		box-shadow: 0 2px 6px rgba(0, 0, 0, 0.05);
	}
</style>